import * as PIXI from 'pixi.js';
import 'pixi-spine';
import { Analytics } from './constants/Analytics';
import Assets from './constants/Assets';
import { PrizeType } from './constants/Prizes';
import BasicScene from './scenes/BasicScene';
import GamePlayScene from './scenes/GamePlayScene';

export interface IGameConfig {
	baseUrl: string;
	prize: {
		type: PrizeType;
		display: string;
		gold?: boolean;
	};
	api: {
		onOptIn: () => void;
		onComplete: () => void;
		onAnalytics: (label: string) => void;
		grab?: () => void;
		skip?: () => void;
		onButtonsActivate?: () => void;
		onButtonsDeactivate?: () => void;
	};
}

export default class Game extends PIXI.Application {
	public static instance: Game;

	public static readonly SCREEN_COLOR = 0x000000;
	public static readonly SCREEN_WIDTH = 720;
	public static readonly SCREEN_HEIGHT = 720;

	private config: IGameConfig;
	private scene: BasicScene;

	constructor(config: IGameConfig) {
		// Surpress Pixi console output
		PIXI.utils.skipHello();

		super({
			view: <HTMLCanvasElement>document.getElementById('canvas'),
			backgroundColor: Game.SCREEN_COLOR,
			width: Game.SCREEN_WIDTH,
			height: Game.SCREEN_HEIGHT,
			resolution: 2,
			transparent: true
		});

		this.config = config;

		// Setup static instance for renderer
		Game.instance = this;

		// Load required assets
		PIXI.loader.reset();
		PIXI.loader.baseUrl = config.baseUrl || '';
		PIXI.loader.add(Assets.ALL);
		PIXI.loader.load(this.handleLoaded.bind(this));

		this.config.api.onAnalytics(Analytics.LOAD_STARTED);
	}

	handleLoaded() {
		this.config.api.onAnalytics(Analytics.LOAD_COMPLETE);

		// Setup update loop
		this.ticker.add(this.update.bind(this));

		this.stage.addChild((this.scene = new GamePlayScene(this.config)));
	}

	update(deltaTime: number) {
		this.scene.update(deltaTime);
	}
}
