import * as React from 'react';
import Game, { IGameConfig } from './Game';

interface IProps {
	config: IGameConfig;
}

interface IState {
	buttonsActive: boolean;
}

export { IGameConfig } from './Game';
export { PrizeType } from './constants/Prizes';

export default class GrabberComponent extends React.Component<IProps, IState> {
	private gameDiv: HTMLDivElement;

	constructor(props: IProps) {
		super(props);

		this.state = {
			buttonsActive: false
		};
	}

	public componentDidMount() {
		const game = new Game(this.props.config);
		this.gameDiv.appendChild(game.view);

		this.props.config.api.onButtonsActivate = () => {
			this.toggleButtons(true);
		};

		this.props.config.api.onButtonsDeactivate = () => {
			this.toggleButtons(false);
		};
	}

	toggleButtons(value: boolean) {
		this.setState({
			buttonsActive: value
		});
	}

	handleGrabClick() {
		this.props.config.api.grab();
	}

	handleSkipClick() {
		this.props.config.api.skip();
	}

	render() {
		const buttonState = this.state.buttonsActive ? 'active' : '';

		return (
			<div className="grabber">
				<div
					className="grabber_game"
					ref={div => {
						this.gameDiv = div;
					}}
				/>
				<div className="grabber_buttons">
					<button
						className={
							'grabber_button grabber_buttons--grab ' +
							buttonState
						}
						onClick={() => this.handleGrabClick()}
					/>
					<button
						className={
							'grabber_button grabber_buttons--skip ' +
							buttonState
						}
						onClick={() => this.handleSkipClick()}
					/>
				</div>
			</div>
		);
	}
}
