import { Expo, TweenLite } from 'gsap';
import { Analytics } from '../constants/Analytics';
import Game, { IGameConfig } from '../Game';
import { BallPitAnimations } from '../gameobjects/BallPit';
import { GrabberAnimations } from '../gameobjects/Grabber';
import BasicScene from './BasicScene';

export default class GamePlayScene extends BasicScene {
	elapsedTime: number;
	activateButtons: boolean;
	move: boolean;

	constructor(config: IGameConfig) {
		super(config);

		this.elapsedTime = 0;

		TweenLite.to(this.grabber.position, 3, {
			y: 558,
			ease: Expo.easeInOut
		}).delay(0);

		this.ballpit.play(BallPitAnimations.INTRO);

		setTimeout(this.ready.bind(this), 2700);

		setTimeout(() => {
			this.showButtons();
		}, 1000);

		this.activateButtons = false;

		this.config.api.grab = this.handleGrabClick.bind(this);
		this.config.api.skip = this.handleSkipClick.bind(this);
	}

	ready() {
		this.activateButtons = true;
		this.move = true;

		this.config.api.onAnalytics(Analytics.GAME_STARTED);
	}

	reveal() {
		this.ballpit.reveal();
		this.confetti.emitt = this.stars.emitt = true;

		this.config.api.onAnalytics(Analytics.GAME_COMPLETE);
		this.config.api.onComplete();
	}

	showButtons() {
		this.config.api.onButtonsActivate();
	}

	hideButtons() {
		this.config.api.onButtonsDeactivate();
	}

	dispatchOptIn() {
		this.config.api.onAnalytics(Analytics.OPT_IN);

		// Opt user into promotion
		this.config.api.onOptIn();
	}

	handleGrabClick() {
		if (!this.activateButtons) return;

		this.config.api.onAnalytics(Analytics.CLICK_PLAY);
		this.dispatchOptIn();

		this.activateButtons = false;

		this.move = false;

		this.grabber.grab();

		setTimeout(() => {
			const xoff = 230 + this.grabber.x - Game.SCREEN_WIDTH / 2;
			this.ballpit.rumble(xoff / 460);
		}, 500);

		setTimeout(() => {
			TweenLite.to(this.grabber.position, 1, {
				x: Game.SCREEN_WIDTH / 2,
				ease: Expo.easeInOut
			});
			setTimeout(() => {
				this.ballpit.play(BallPitAnimations.STEPS_ASCEND_EMPTY);

				this.hideButtons();

				// Swap Z-Index
				this.removeChild(this.grabber);
				this.addChild(this.grabber);

				TweenLite.to(this.grabber.position, 1, {
					y: 558,
					ease: Expo.easeInOut
				}).delay(1);

				setTimeout(() => {
					this.grabber.play(GrabberAnimations.GRABBER_DROP_RETRACT);

					setTimeout(() => {
						this.removeChild(this.grabber);
						this.reveal();
					}, 1500);
				}, 2000);
			}, 1000);
		}, 2000);
	}

	handleSkipClick() {
		if (!this.activateButtons) return;

		this.config.api.onAnalytics(Analytics.CLICK_SKIP);
		this.dispatchOptIn();

		this.hideButtons();

		this.move = false;
		this.activateButtons = false;

		this.grabber.play(GrabberAnimations.GRABBER_SKIP_RETRACT);

		setTimeout(() => {
			this.ballpit.play(BallPitAnimations.STEPS_ASCEND);
			setTimeout(() => {
				this.reveal();
			}, 1500);
		}, 500);
	}

	public update(deltaTime: number) {
		super.update(deltaTime);

		if (this.move) {
			this.elapsedTime += deltaTime;
			this.grabber.x =
				Game.SCREEN_WIDTH / 2 + Math.sin(this.elapsedTime / 50) * 230;

			const current = this.grabber.spine.state.getCurrent(0).animation
				.name;
			let next = null;
			const padding = 100;
			if (this.grabber.x > Game.SCREEN_WIDTH / 2 + padding)
				next = GrabberAnimations.GRABBER_MOVE_R;
			if (this.grabber.x < Game.SCREEN_WIDTH / 2 - padding)
				next = GrabberAnimations.GRABBER_MOVE_L;

			if (next != null && current != next) {
				this.grabber.play(next);
			}
		}
	}
}
