import * as Pixi from 'pixi.js';
import 'pixi-particles';
import Assets, { SpriteSheets } from '../constants/Assets';

export default class ConfettiParticles extends Pixi.Container {
	static readonly ASSET_CONFETTI_RED = 'confetti_red.png';
	static readonly ASSET_CONFETTI_SILVER = 'confetti_silver.png';
	static readonly ASSET_CONFETTI_GOLD = 'confetti_gold.png';

	emitter: Pixi.particles.Emitter;

	constructor(gold: boolean = false) {
		super();

		this.emitter = new Pixi.particles.Emitter(
			this,
			gold
				? [Pixi.Texture.fromFrame(SpriteSheets.Particles.CONFETTI_GOLD)]
				: [
						Pixi.Texture.fromFrame(
							SpriteSheets.Particles.CONFETTI_RED
						),
						Pixi.Texture.fromImage(
							SpriteSheets.Particles.CONFETTI_SILVER
						)
				  ],
			Pixi.loader.resources[Assets.Particles.Confgis.CONFETTI].data
		);

		this.emitt = false;

		const sprite = new Pixi.Sprite(Pixi.Texture.fromImage(Assets.ALPHA));
		this.addChild(sprite);
		this.mask = sprite;
	}

	public set emitt(value: boolean) {
		this.emitter.emit = value;
	}

	public update(deltaTime: number) {
		this.emitter.update(deltaTime / 70);
	}
}

export class StarsParticles extends Pixi.Container {
	static readonly ASSET_GLINT_GOLD = 'glint.png';

	emitter: Pixi.particles.Emitter;

	constructor(image?: Pixi.Texture) {
		super();

		this.emitter = new Pixi.particles.Emitter(
			this,
			[Pixi.Texture.fromImage(SpriteSheets.Particles.GLINT)],
			Pixi.loader.resources[Assets.Particles.Confgis.STARS].data
		);

		this.emitt = false;

		const sprite = new Pixi.Sprite(Pixi.Texture.fromImage(Assets.ALPHA));
		this.addChild(sprite);
		this.mask = sprite;
	}

	public set emitt(value: boolean) {
		this.emitter.emit = value;
	}

	public update(deltaTime: number) {
		this.emitter.update(deltaTime / 70);
	}
}
