import { Sprite, Texture, Container } from 'pixi.js';

export const FontMap = {
	'0': '0.png',
	'1': '1.png',
	'2': '2.png',
	'3': '3.png',
	'4': '4.png',
	'5': '5.png',
	'6': '6.png',
	'7': '7.png',
	'8': '8.png',
	'9': '9.png',
	'': '.png',
	'': '.png'
};

export default class Font extends Container {
	constructor(text: string) {
		super();

		this.scale.set(0.5, 0.5);

		const characters = text.split('').filter(char => {
			if (!FontMap[char]) {
				console.warn(`Font Sheet missing character ${char}`);
				return false;
			}

			return true;
		});

		const sprites = characters.map(char => {
			return new Sprite(Texture.fromFrame(FontMap[char]));
		});

		let width = 0;

		sprites.forEach((sprite, index) => {
			sprite.position.x = width;

			width += sprite.width;

			this.addChild(sprite);
		});

		sprites.forEach((sprite, index) => {
			sprite.x -= width / 2;
			sprite.y -= sprite.height;
		});
	}
}
