import * as Pixi from 'pixi.js';
import { TweenMax, TimelineMax, Sine } from 'gsap';
import Assets, { SpriteSheets } from '../constants/Assets';
import { Container, spine, loader } from 'pixi.js';
import { IGameConfig } from '../Game';

export enum GrabberAnimations {
	GRABBER_IDLE = 'grabber_idle',
	GRABBER_INTRO = 'grabber_intro',
	GRABBER_DROP_RETRACT = 'grabber_drop_retract',
	GRABBER_SKIP_RETRACT = 'grabber_skip_retract',
	GRABBER_MOVE_R = 'grabber_move_R',
	GRABBER_MOVE_L = 'grabber_move_L',
	GRABBER_GRAB_BALL_1 = 'grabber_grab_ball_01',
	GRABBER_GRAB_BALL_2 = 'grabber_grab_ball_02',
	GRABBER_GRAB_BALL_3 = 'grabber_grab_ball_03',
	GRABBER_GRAB_BALL_4 = 'grabber_grab_ball_04'
}

export default class Grabber extends Container {
	public static readonly ASSET_DISPLAY_KEY = 'Assets/Numbers';
	public static readonly ASSET_GOLD_KEYS = {
		BOTTOM: 'assets/grabber_ball_bottom',
		BACK: 'assets/ball back',
		TOP: 'assets/grabber_ball_top'
	};

	public spine: spine.Spine;

	constructor(config: IGameConfig) {
		super();

		const resource = loader.resources[Assets.Spine.GRABBER].spineData;
		this.spine = new spine.Spine(resource);
		this.spine.scale.set(0.5, 0.5);
		this.addChild(this.spine);

		this.play(GrabberAnimations.GRABBER_IDLE);

		if (config.prize.gold) {
			const bottom = Pixi.Texture.fromImage(SpriteSheets.Gold.BOTTOM);
			const top = Pixi.Texture.fromImage(SpriteSheets.Gold.TOP);
			const background = Pixi.Texture.fromImage(
				SpriteSheets.Gold.BACKGROUND
			);

			this.spine.hackTextureBySlotName(
				Grabber.ASSET_GOLD_KEYS.BOTTOM,
				bottom,
				new PIXI.Rectangle(0, 0, bottom.width, bottom.height)
			);

			this.spine.hackTextureBySlotName(
				Grabber.ASSET_GOLD_KEYS.BACK,
				background,
				new PIXI.Rectangle(0, 0, background.width, background.height)
			);

			this.spine.hackTextureBySlotName(
				Grabber.ASSET_GOLD_KEYS.TOP,
				top,
				new PIXI.Rectangle(0, 0, top.width, top.height)
			);
		}
	}

	public play(animation: GrabberAnimations, loop: boolean = false) {
		this.spine.state.setAnimation(0, animation, loop);
	}

	public grab() {
		const shifts = [
			GrabberAnimations.GRABBER_GRAB_BALL_1,
			GrabberAnimations.GRABBER_GRAB_BALL_2,
			GrabberAnimations.GRABBER_GRAB_BALL_3,
			GrabberAnimations.GRABBER_GRAB_BALL_4
		];

		// Move to MathUtils.randomElement
		this.play(shifts[Math.round(Math.random() * (shifts.length - 1))]);
	}
}
