export default class ObjectUtils {
	public static flattenStrings(value: object): string[] {
		const traverse = (object, result) => {
			for (let obj of Object.keys(object)) {
				var value = object[obj];

				if (typeof value == 'string') {
					result.push(value);
				}

				if (value instanceof Object) {
					traverse(value, result);
				}
			}
		};

		const result = [];
		traverse(value, result);
		return result;
	}
}
