import * as Pixi from 'pixi.js';
import 'pixi-spine';
import Assets, { SpriteSheets } from '../constants/Assets';
import { PrizeType } from '../constants/Prizes';
import Game, { IGameConfig } from '../Game';
import Font from '../ui/Font';

export enum BallPitAnimations {
	PIT_STILL = 'pit_still',
	INTRO = 'intro',
	STEPS_ASCEND_EMPTY = 'steps_ascend_empty',
	STEPS_ASCEND = 'steps_ascend',
	STEPS_REVEAL_WIN_CASH = 'steps_reveal_win_cash',
	STEPS_REVEAL_WIN_FREE_SPINS = 'steps_reveal_win_free_spins',
	STEPS_REVEAL_WIN_WIN_SPINS = 'steps_reveal_win_win_spins',
	STEPS_SHIFT_1 = 'ball_shift_01',
	STEPS_SHIFT_2 = 'ball_shift_02',
	STEPS_SHIFT_3 = 'ball_shift_03',
	STEPS_SHIFT_4 = 'ball_shift_04',
	STEPS_SHIFT_5 = 'ball_shift_05'
}

export default class BallPit extends Pixi.Container {
	public static readonly ASSET_DISPLAY_KEY = 'Assets/Numbers';
	public static readonly ASSET_GOLD_KEYS = {
		BOTTOM: 'Assets/grabber_ball_bottom',
		BACK: 'Assets/ball back',
		TOP: 'Assets/grabber_ball_top'
	};

	public spine: Pixi.spine.Spine;

	config: IGameConfig;

	constructor(config: IGameConfig) {
		super();

		this.config = config;

		const resource = Pixi.loader.resources[Assets.Spine.BALL_PIT].spineData;

		this.spine = new Pixi.spine.Spine(resource);
		this.spine.scale.set(0.5, 0.5);
		this.addChild(this.spine);

		this.play(BallPitAnimations.PIT_STILL);

		this.switchPrizeText(this.config.prize.display);

		if (config.prize.gold) {
			const bottom = Pixi.Texture.fromImage(SpriteSheets.Gold.BOTTOM);
			const top = Pixi.Texture.fromImage(SpriteSheets.Gold.TOP);
			const background = Pixi.Texture.fromImage(
				SpriteSheets.Gold.BACKGROUND
			);

			this.spine.hackTextureBySlotName(
				BallPit.ASSET_GOLD_KEYS.BOTTOM,
				bottom,
				new PIXI.Rectangle(0, 0, bottom.width, bottom.height)
			);

			this.spine.hackTextureBySlotName(
				BallPit.ASSET_GOLD_KEYS.BACK,
				background,
				new PIXI.Rectangle(0, 0, background.width, background.height)
			);

			this.spine.hackTextureBySlotName(
				BallPit.ASSET_GOLD_KEYS.TOP,
				top,
				new PIXI.Rectangle(0, 0, top.width, top.height)
			);
		}
	}

	public play(animation: BallPitAnimations, loop: boolean = false) {
		this.spine.state.setAnimation(0, animation, loop);
	}

	public reveal() {
		let animation: BallPitAnimations = null;

		if (this.config.prize.type == PrizeType.FREE_CASH)
			animation = BallPitAnimations.STEPS_REVEAL_WIN_CASH;

		if (this.config.prize.type == PrizeType.FREE_SPINS)
			animation = BallPitAnimations.STEPS_REVEAL_WIN_FREE_SPINS;

		if (this.config.prize.type == PrizeType.WIN_SPINS)
			animation = BallPitAnimations.STEPS_REVEAL_WIN_WIN_SPINS;

		this.play(animation);
	}

	private switchPrizeText(display: string) {
		const renderTexture = PIXI.RenderTexture.create(512 * 2, 512);

		const text = new Font(display);

		if (
			this.config.prize.type == PrizeType.WIN_SPINS ||
			this.config.prize.type == PrizeType.FREE_SPINS
		) {
			text.scale.set(0.5, 0.5);
			text.position.y = 345;
		}

		if (this.config.prize.type == PrizeType.FREE_CASH) {
			text.scale.set(0.4, 0.4);
			text.position.y = 340;
		}

		text.position.x = 512;

		Game.instance.renderer.render(text, renderTexture);

		this.spine.hackTextureBySlotName(
			BallPit.ASSET_DISPLAY_KEY,
			renderTexture,
			new PIXI.Rectangle(0, 0, renderTexture.width, renderTexture.height)
		);
	}

	public rumble(float: number = 0.5) {
		const shifts = [
			BallPitAnimations.STEPS_SHIFT_1,
			BallPitAnimations.STEPS_SHIFT_2,
			BallPitAnimations.STEPS_SHIFT_3,
			BallPitAnimations.STEPS_SHIFT_4,
			BallPitAnimations.STEPS_SHIFT_5
		];

		this.play(shifts[Math.round(float * (shifts.length - 1))]);
	}
}
