import { Container } from 'pixi.js';
import Assets from '../constants/Assets';
import Game, { IGameConfig } from '../Game';
import BallPit from '../gameobjects/BallPit';
import Grabber from '../gameobjects/Grabber';
import ConfettiParticles, { StarsParticles } from '../gameobjects/Particles';

export default class BasicScene extends Container {
	config: IGameConfig;

	grabber: Grabber;
	ballpit: BallPit;

	confetti: ConfettiParticles;
	stars: StarsParticles;

	constructor(config: IGameConfig) {
		super();

		this.config = config;

		const mask = new PIXI.Graphics();
		mask.drawRect(0, 0, Game.SCREEN_WIDTH, 560);
		this.addChild(mask);

		this.addChild((this.grabber = new Grabber(config)));
		this.grabber.y = 0;
		this.grabber.x = Game.SCREEN_WIDTH / 2;
		this.grabber.mask = mask;

		this.ballpit = new BallPit(config);
		this.ballpit.position.set(Game.SCREEN_WIDTH / 2, 560);
		this.ballpit.mask = mask;
		this.addChild(this.ballpit);

		const rim = PIXI.Sprite.fromImage(Assets.RIM);
		rim.position.set(Game.SCREEN_WIDTH / 2 - 363, 518);
		rim.scale.set(0.5);
		this.addChild(rim);

		this.addChild(
			(this.confetti = new ConfettiParticles(config.prize.gold))
		);
		this.confetti.position.set(0, -20);
		this.confetti.scale.set(1, 1);

		this.addChild((this.stars = new StarsParticles()));
		this.stars.position.set(200, 150);
	}

	public update(deltaTime: number) {
		this.confetti.update(deltaTime);
		this.stars.update(deltaTime);
	}
}
