import * as React from 'react';
import ReactDOM from 'react-dom';
import GrabberComponent from '../src';
import { IGameConfig } from '../src/Game';
import { PrizeType } from '../src/constants/Prizes';

const config: IGameConfig = {
	baseUrl: '',
	prize: {
		type: PrizeType.FREE_CASH,
		display: '10'
	},
	api: {
		onAnalytics: label => {
			console.info(
				` Track: %c${label}`,
				'color: #afce54; font-weight: bold;'
			);
		},
		onComplete: () => {
			console.info(' Game Complete!');
		},
		onOptIn: () => {
			console.info(' User has opted in');
		}
	}
};

interface IState {
	gameActive: boolean;
	modes: { [index: string]: { type: PrizeType; display: string, gold?: boolean } };
}

class GrabberPlaygroundComponent extends React.Component<{}, IState> {
	constructor(props, state) {
		super(props, state);

		this.state = {
			gameActive: false,
			modes: {
				'5 Free Spins': {
					type: PrizeType.FREE_SPINS,
					display: '5'
				},
				'40 Free Spins': {
					type: PrizeType.FREE_SPINS,
					display: '40'
				},
				'1 Free Cash': {
					type: PrizeType.FREE_CASH,
					display: '1'
				},
				'10 Free Cash': {
					type: PrizeType.FREE_CASH,
					display: '10'
				},
				'1000 Free Cash': {
					type: PrizeType.FREE_CASH,
					display: '1000'
				},
				'10 Win Spin': {
					type: PrizeType.WIN_SPINS,
					display: '10'
				},
				'25 Win Spin': {
					type: PrizeType.WIN_SPINS,
					display: '25'
				},
				'1000 Free Cash Gold': {
					type: PrizeType.FREE_CASH,
					display: '1000',
					gold: true,
				}
			}
		};
	}

	start(key) {
		config.prize = key;

		this.setState({
			gameActive: true
		});
	}

	render() {
		return (
			<div>
				{!this.state.gameActive && (
					<div className="menuList list-group">
						{Object.keys(this.state.modes).map((key, index) => {
							return (
								<a
									className="list-group-item list-group-item-action"
									onClick={() =>
										this.start(this.state.modes[key])
									}
									key={key}
								>
									{key}
								</a>
							);
						})}
					</div>
				)}
				{this.state.gameActive && <GrabberComponent config={config} />}
			</div>
		);
	}
}

ReactDOM.render(<GrabberPlaygroundComponent />, document.getElementById('app'));

// HMR Reload window
if (module.hot) {
	module.hot.dispose(() => {
		window.location.reload(true);
	});

	module.hot.accept(() => {});
}
